.corpo-pagina {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 10px;
}

.janela {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    width: 100%;
    background-color: #F1F2F2;
    gap: 5px;
    border-radius: 8px;
    padding: 20px;
}

.grupo-select {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    gap: 5px
}

.grupo-input-ano {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    gap: 5px;
}

label {
    font-size: 14px;
    font-weight: bold;
}

input {
    width: 412px;
    height: 25px;
}

select {
    height: 30px;
    min-width: 420px;
}

input[type=file]::file-selector-button {
    height: 20px;
    margin: 5px;
}

.input-document {
    min-width: 544px;
    height: 30px;
    background-color: #FFF;
    border: 1px solid black;
}

button {
    width: 545px;
    height: 30px;
    background-color: #4DBCE4;
    border: 0px;
    color: #FFF;
}

.label-select-box {
    font-family: Arial, Helvetica, sans-serif;
}

text {
    color: #4CBC73;
    font-weight: bold;
    font-size: 16px;
}

button:active {
    background-color: #4DBCE440;
}

.titulo-abas-label {
    margin-top: 40px;
    font-family: Arial, Helvetica, sans-serif;
}

.container-abas {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
    gap: 3px;
    max-width: 90%;
    border: 1px solid #474747;
    border-radius: 5px;

}

.item-aba {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-color: #4CBC73;
    padding: 5px;
    min-width: 35px;
}

.item-aba-texto {
    color: black;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
}

.item-aba-erro {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-color: #D76561;
    padding: 5px;
    min-width: 35px;
}

.mensagem-info-texto {
    margin-top: 40px;
    color: black;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}

.mensagem-arquivo-pronto {
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
}